.text120,
.text80,
.text46,
.text h3,
.h1,
.h2{
  font-family: $font2;
  font-weight: 700;
}

.h1{
  font-size: 11.25em;
}

.text120{
  font-size: 7.5em;
}

.text80{
  font-size: 5em;
}

.h2{
  font-size: 6.25em;
}

.text h3,
.text46{
  font-size: 2.875em;
}

.text22{
  font-size: 1.375em;
}

.text p,
.text20{
  font-size: 1.25em;
  line-height: 160%;
}

.text19{
  font-size: 1.1875em;
  line-height: 120%;
}

.text18{
  font-size: 1.125em;
  line-height: 120%;
}

.text17{
  font-size: 1.0625em;
  line-height: 120%;
}

.text16{
  font-size: 1em;
  line-height: 120%;
}

.text15{
  font-size: .9375em;
  line-height: 120%;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.text13{
  font-size: .8125em;
  line-height: 120%;
}

.text12{
  font-size: .75em;
  line-height: 120%;
}

.text11{
  font-size: .6875em;
  line-height: 120%;
}

.text10{
  font-size: .625em;
  line-height: 120%;
}

.extra-bold-text{
  font-weight: 800;
}

.bold-text{
  font-weight: 700;
}

.semi-bold-text{
  font-weight: 600;
}

.medium-text{
  font-weight: 500;
}

.regular-text{
  font-weight: 400;
}

.light-text{
  font-weight: 300;
}

.font2{
  font-family: $font2;
}


@media screen and (max-width: 992px){
  .h1{
    font-size: 5.625em;
  }

  .text120,
  .text80{
    font-size: 3.75em;
  }

  .h2{
    font-size: 2.75em;
  }

  .text20-tablet{
    font-size: 1.25em;
  }

  .text19-tablet{
    font-size: 1.1875em;
  }

  .text18-tablet{
    font-size: 1.125em;
  }

  .text17-tablet{
    font-size: 1.0625em;
  }

  .text16-tablet{
    font-size: 1em;
  }

  .text15-tablet{
    font-size: .9375em;
  }

  .text14-tablet{
    font-size: .875em;
  }

  .text13-tablet{
    font-size: .8125em;
  }

  .text12-tablet{
    font-size: .75em;
  }

  .text11-tablet{
    font-size: .6875em;
  }

  .text10-tablet{
    font-size: .625em;
  }

}


@media screen and (max-width: 576px){
  .text14-mob{
    font-size: .875em;
  }
}
