.footer{
  padding: 5em 0 2.5em;
  overflow: hidden;
  .socials{
    justify-content: center;
  }
  &__c{
    margin-top: 2.5em;
    text-align: center;
  }
}

.footer-nav{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 1.25em;
  width: calc(100% + 3.75em);
  &__link{
    margin: 1.25em 3.75em 0 0;
    font-family: $font2;
    font-weight: 700;
    color: rgba($secondary,.6);
    span{
      font-size: 1.125em;
    }
    &:hover{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding-top: 3.75em;
  }
}


@media screen and (max-width: 576px){
  .footer{
    .socials{
      justify-content: space-between;
    }
  }
}
