.main{
  padding: 16.5em 0;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    height: 7em;
    width: 100%;
    background-image: url(../img/bottom.svg);
    background-size: 100% auto;
    background-repeat: no-repeat;
    background-position: left bottom;
    left: 0;
    bottom: -2px;
    pointer-events: none;
    z-index: 2;
  }
  &__content{
    max-width: 44.375em;
    position: relative;
    z-index: 3;
  }
  &__desc{
    margin-top: .25em;
  }
  &__nav{
    display: flex;
    margin-top: 3.75em;
  }
  &__video{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    &:before{
      z-index: 2;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgba($black,.4);
    }
    img,
    video{
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.let-play{
  position: relative;
  &__bg{
    width: 100%;
    display: block;
    &._view2{
      display: none;
    }
  }
  &__wrap{
    position: absolute;
    left: 0;
    width: 100%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 2;
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__img{
    width: 26.4375em;
    margin-right: 11em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__top{
    max-width: 30em;
  }
  &__desc{
    margin-top: 1.25em;
    a,
    button{
      color: $primary;
      &:hover{
        color: $primaryHover;
      }
    }
  }
}

.stat{
  padding: 7.125em 0;
  &__items{
    display: flex;
    justify-content: center;
  }
}

.stat-item{
  display: flex;
  align-items: center;
  @include lastM(0 9.375em 0 0);
  &__icon{
    width: 4.25em;
    margin-right: 1.25em;
  }
  &__title{
    font-size: 2.25em;
    text-transform: uppercase;
  }
  &__num{
    font-size: 5.125em;
    font-weight: 700;
    font-family: $font2;
    color: $primary;
    margin-left: .25em;
  }
}


@media screen and (max-width: 992px){
  .main{
    padding: 12.875em 0 10.375em;
    text-align: center;
    &:before{
      background-image: url(../img/bottom2.svg);
    }
    &__content{
      max-width: 32em;
      margin: 0 auto;
    }
    &__nav{
      margin-top: 1.25em;
      justify-content: center;
    }
  }

  .let-play{
    &__bg{
      &._view1{
        display: none;
      }
      &._view2{
        display: block;
      }
    }
    &__wrap{
      margin-top: -.25em;
    }
    &__inner{
      position: relative;
    }
    &__top{
      min-height: 7.5em;
      padding-left: 8.75em;
      max-width: unset;
      display: flex;
      align-items: center;
    }
    &__img{
      width: 7.5em;
      position: absolute;
      top: 0;
      left: 0;
    }
  }

  .stat{
    padding: 4.75em 0;
  }

  .stat-item{
    @include lastM(0 2.5em 0 0);
    &__icon{
      width: 3.625em;
    }
    &__title{
      font-size: 1.875em;
    }
    &__num{
      font-size: 4.25em;
      margin-left: .3em;
    }
  }
}


@media screen and (max-width: 576px){
  .let-play{
    &__top{
      align-items: flex-start;
    }
  }

  .stat{
    &__items{
      flex-direction: column;
      align-items: flex-start;
    }
  }

  .stat-item{
    @include lastM(0 0 2.5em);
  }
}
