.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  padding: 3.125em 0;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
  &__logo{
    display: block;
    width: 13.125em;
    margin-right: auto;
    img{
      width: 100%;
      display: block;
    }
  }
  &__btn{
    padding: 0 1.25em;
    height: 3.5em;
    span{
      font-size: 1.375em;
    }
    img{
      width: 1.75em;
      margin-right: .625em;
    }
    br{
      display: none;
    }
  }
  .socials{
    display: none;
  }
}

.header-user{
  font-weight: 700;
  padding: 0 1.25em;
  height: 3.5em;
  background-color: $primary;
  display: flex;
  align-items: center;
  &__img{
    width: 2.125em;
    height: 2.125em;
    object-fit: cover;
    border-radius: 50%;
    margin-right: .625em;
  }
  &__logout{
    width: 1.25em;
    margin-left: .625em;
    @include center;
    flex-shrink: 0;
  }
}

.burger{
  display: none;
}

.menu{
  margin-right: 3.75em;
  &__close{
    display: none;
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    @include lastM(0 3.75em 0 0);
    font-family: $font2;
    font-weight: 700;
    span{
      font-size: 1.375em;
    }
    &:hover{
      color: $primary;
    }
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item{
    display: flex;
    align-items: center;
    font-family: $font2;
    font-weight: 700;
    @include lastM(0 3.75em 0 0);
    text-transform: uppercase;
  }
  &__icon{
    width: 2.875em;
    height: 2.875em;
    @include center;
    background-color: $primary;
    margin-right: .9375em;
    img{
      max-width: 1.625em;
      max-height: 1.5em;
    }
  }
}



@media screen and (max-width: 992px){
  .header{
    padding: .9375em 0;
    &__logo{
      width: 9.125em;
    }
    &__btn{
      height: 3.25em;
      padding: 0 .875em;
      span{
        font-size: 1.0625em;
      }
      img{
        width: 1.375em;
      }
    }
    .socials{
      display: flex;
      justify-content: center;
      padding-top: 2em;
      margin-top: auto;
    }
    &._open{
      .menu{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }

  .burger{
    display: flex;
    width: 3.25em;
    height: 3.25em;
    padding: 0;
    margin-left: .3125em;
    img{
      width: 2em;
    }
  }

  .menu{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    padding: 12.5em .9375em 2.5em;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    transition: all .5s;
    opacity: 0;
    pointer-events: none;
    &__close{
      position: fixed;
      top: .625em;
      right: .625em;
      width: 2.75em;
      height: 2.75em;
      @include center;
      img{
        width: 1.5em;
      }
    }
  }

  .nav{
    flex-direction: column;
    &__link{
      @include lastM(0 0 3.75em);
      span{
        font-size: 1.6875em;
      }
    }
  }

  .socials{
    &__item{
      @include lastM(0 1.25em 0 0);
    }
    &__icon{
      margin: 0;
    }
    &__title{
      display: none;
    }
  }

  .header-user{
    height: 3.25em;
    padding: 0 .875em;
    &__title{
      font-size: 1.0625em;
    }
    &__img{
      width: 1.75em;
      height: 1.75em;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__btn{
      text-align: left;
      br{
        display: unset;
      }
    }
  }

  .header-user{
    &__title{
      max-width: 2.875em;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    &__logout{
      margin-left: .25em;
    }
  }
}
