.support{
  padding: 5.625em 0 2.5em;
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 27.8125em - 6.25em) 27.8125em;
    justify-content: space-between;
    gap: 2.5em;
  }
  .faq__items{
    margin-top: 2.5em;
  }
}

.req-form{
  padding: 2.5em;
  background-color: $primary;
  position: relative;
  &:before,
  &:after{
    content: '';
    position: absolute;
    left: 0;
    width: 100%;
    height: .625em;
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center;
  }
  &:before{
    background-image: url(../img/form-decor.svg);
    bottom: 100%;
    margin-bottom: -1px;
  }
  &:after{
    background-image: url(../img/form-decor2.svg);
    top: 100%;
    margin-top: -1px;
  }
  &__fields{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
  &__btn{
    height: 3.5em;
    span{
      font-size: 1.375em;
    }
  }
}


@media screen and (max-width: 992px){
  .support{
    padding: 3.75em 0 0;
    &__grid{
      grid-template-columns: 100%;
    }
    .faq__items{
      margin-top: 1.25em;
    }
    .qa-card{
      &:last-child{
        padding-bottom: 1.25em;
        border-bottom: 2px solid rgba(255, 255, 255, 0.05);
      }
    }
  }

  .req-form{
    padding: 1.875em;
    &:before,
    &:after{
      height: 1.125em;
    }
  }
}


@media screen and (max-width: 576px){
  .req-form{
    &:before,
    &:after{
      height: .525em;
    }
  }
}
