.profile-main{
  min-height: 37.5em;
  padding: 0;
}

.profile{
  z-index: 3;
  position: relative;
  &__bg{
    background: rgba(255, 255, 255, 0.1);
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    backdrop-filter: blur(2px);
  }
  &__inner{
    margin-top: -19.25em;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 100%;
      height: 2.0625em;
      left: 0;
      background-size: auto 100%;
      background-repeat: no-repeat;
      background-position: center;
    }
    &:before{
      bottom: 100%;
      background-image: url(../img/profile-bg.svg);
    }
    &:after{
      top: 100%;
      background-image: url(../img/profile-bg2.svg);
    }
  }
  &__top,
  &__bottom{
    padding: 2.5em 12.8125em;
  }
  &__bottom{
    border-top: 2px solid rgba(#fff,.05);
  }
}

.user-info{
  margin-top: 1.25em;
  display: grid;
  grid-template-columns: 14em calc(100% - 16.5em);
  justify-content: space-between;
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: 1.25em;
    }
  }
  &__content{
    align-self: center;
  }
  &__items{
    margin-top: 1.5625em;
    @include grid100gap(1.5625em);
  }
  &__balance{
    margin-top: 1.5625em;
    @include grid100gap(.3125em);
    &-title{
      font-size: 1em;
      line-height: 120%;
      color: $primary;
    }
    &-num{
      font-size: 1.875em;
      font-weight: 700;
      font-family: $font2;
    }
  }
}

.user-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 1.75em;
    margin-right: .625em;
  }
  &__title{
    font-weight: 700;
  }
  &__desc{
    margin-left: .55em;
  }
  &__status{
    padding: 0 .625em;
    margin-left: .625em;
    height: 1.8125em;
    @include center;
    text-transform: uppercase;
    font-weight: 700;
    span{
      font-size: 1em;
    }
  }
}

.shop{
  margin-top: 1.25em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 6.75em / 4));
  gap: 2.25em;
}

.shop-card{
  &__img{
    @include imgCover(160%);
    border-radius: 1.25em;
    opacity: .5;
  }
  &__btn{
    width: 100%;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){
  .profile-main{
    min-height: 15.125em;
  }

  .profile{
    &__inner{
      margin-top: -3.625em;
      &:before,
      &:after{
        height: 4.5em;
      }
      &:before{
        background-image: url(../img/profile-bg3.svg);
      }
      &:after{
        transform: translateY(-.25px);
        background-image: url(../img/profile-bg4.svg);
      }
    }
    &__top{
      padding: 1.25em 1.25em 1.875em;
    }
    &__bottom{
      padding: 1.875em 1.25em 1.25em;
    }
  }

  .user-info{
    margin-top: .625em;
    grid-template-columns: 10em calc(100% - 11.25em);
    padding-top: 2.625em;
    position: relative;
    &__top{
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      .text46{
        font-size: 2em;
      }
    }
    &__img{
      img{
        border-radius: .625em;
      }
    }
    &__items{
      margin-top: 0;
      gap: 1.25em;
    }
    &__balance{
      margin-top: 1.25em;
    }
  }

  .shop{
    margin-top: .625em;
    grid-template-columns: repeat(4,calc(25% - 3.75em / 4));
    gap: 1.25em;
  }

  .shop-card{
    &__img{
      border-radius: .625em;
    }
    &__btn{
      margin-top: .625em;
    }
  }
}


@media screen and (max-width: 576px){
  .profile-main{
    &:before{
      display: none;
    }
  }

  .profile{
    &__inner{
      &:before{
        height: 2.0625em;
      }
      &:after{
        height: 2.375em;

        z-index: -1;
      }
    }
  }

  .user-info{
    &__balance-num{
      font-size: 1.625em;
    }
  }

  .user-item{
    flex-wrap: wrap;
    &__icon{
      width: 1.25em;
      margin-right: .3125em;
    }
    &__desc{
      width: 100%;
      margin: .3125em 0 0;
      font-size: 1em;
    }
    &__title{
      font-size: 1em;
    }
    &__status{
      margin: .3125em 0 0;
      height: 1.625em;
      span{
        font-size: .875em;
      }
    }
  }

  .shop{
    grid-template-columns: repeat(2,calc(50% - .625em));
  }
}
