.rules{
  padding: 8.75em 0 2.5em;
}

.text {
  h3{
    margin-bottom: .44em;
  }
  p{
    @include lastM(0 0 1.125em);
  }
  hr{
    display: block;
    margin: 2.5em 0;
    border: none;
    border-bottom: 2px solid rgba(#fff,.5);
  }
}


@media screen and (max-width: 992px){
  .rules{
    padding: 2.5em 0 0;
  }

  .text{
    h3{
      font-size: 2em;
      margin-bottom: .3125em;
    }
    p{
      font-size: 1em;
    }
    hr{
      margin: 1.25em 0;
    }
  }
}


