@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.875em;
    height: 4.125em;
    font-weight: 700;
    font-family: $font2;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.875em;
        line-height: 110%;
    }
}

.m-btn-red{
    background-color: $primary;
    color: $secondary;
    &:hover{
        background-color: $primaryHover;
        color: $secondary;
    }
}

.m-btn-black{
    background-color: $black;
    color: $secondary;
    &:hover{
        background-color: darken($black,10%);
        color: $secondary;
    }
}
