.fg {
  position: relative;
  margin: 0;
  input,
  textarea{
    border: 1px solid $secondary;
    padding: 0 .91em;
    height: 2.54em;
    width: 100%;
    font-size: 1.375em;
    font-family: $font2;
    background-color: transparent;
    color: $secondary;
    &::placeholder {
      color: $secondary;
    }
  }
  textarea{
    padding: .91em;
    height: 7.27em;
    resize: none;
  }
  label{
    display: block;
    margin-bottom: .625em;
    font-size: 1em;
    line-height: 120%;
  }
  &__error-text{
    position: absolute;
    top: 1.825em;
    right: 1.25em;
    display: flex;
    align-items: center;
    height: 3.5em;
    opacity: 0;
    pointer-events: none;
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
  &__clear{
    width: 1em;
    margin-right: .3125em;
    img{
      width: 100%;
      display: block;
    }
  }
  &._error{
    textarea,
    input{
      background: rgba(255, 255, 255, 0.1);
    }
    .fg__error-text{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.file-input{
  position: relative;
  border: 1px solid $secondary;
  min-height: 3.5em;
  &._uploaded{
    .label-container{
      opacity: 1;
      pointer-events: unset;
    }
    .file-input__inner{
      opacity: 0;
    }
  }
  .input-container{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  .input-hidden{
    min-width: unset;
    height: 100%;
    cursor: pointer;
    opacity: 0;
  }
  .input-visible{
    display: none;
  }
  .label-container{
    z-index: 3;
    position: absolute;
    top: 1.75em;
    right: 1.25em;
    margin: 0;
    opacity: 0;
    pointer-events: none;
    label{
      display: none;
    }
  }
  .clear-button{
    color: transparent !important;
    width: 1.25em;
    height: 1.25em;
    background-image: url(../img/delete.svg);
    @include bgImgSize(100%);
    font-size: 1em;
  }
  .image-preview{
    height: unset;
    padding: 1.125em;
    border-radius: 0;
    background: unset !important;
    margin-top: -.625em;
    pointer-events: none;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
  }
  .image-preview-item{
    background: unset !important;
    box-shadow: unset;
    font-size: 1.125em;
    font-family: $font2;
    text-transform: uppercase;
    height: unset;
    width: auto;
    margin: .55em .55em 0 0;
    &:before{
      content: attr(data-name);
    }
    &-clear{
      display: none;
    }
  }
  &__inner{
    @include centerAbsolute;
    width: 100%;
    @include center;
    font-weight: 700;
    pointer-events: none;
  }
}
