.main2{
  padding-bottom: 11em;
  .main__content{
    max-width: 100%;
    text-align: center;
  }
}

.faq{
  padding: 8.75em 0 2.5em;
}

.qa-card{
  &._active{
    .qa-card__btn{
      ._hide{
        opacity: 0;
      }
    }
  }
  &:not(:last-child){
    padding-bottom: 2.5em;
    margin-bottom: 2.5em;
    border-bottom: 2px solid rgba(255, 255, 255, 0.05);
  }
  &__top{
    display: flex;
  }
  &__btn{
    width: 3em;
    height: 3em;
    background-color: $primary;
    flex-shrink: 0;
    @include center;
    margin-right: 1.25em;
    svg{
      width: 1.75em;
    }
  }
  &__title{
    padding-top: .075em;
  }
  .spoiler__wrap{
    padding: 1.5em 0 0 4.25em;
  }
}


@media screen and (max-width: 992px){
  .main2{
    padding: 8.375em 0 3.25em;
  }

  .faq{
    padding: 3.75em 0 0;
  }

  .qa-card{
    &:not(:last-child){
      padding-bottom: 1.25em;
      margin-bottom: 1.25em;
    }
    &__btn{
      width: 2.25em;
      height: 2.25em;
      svg{
        width: 1.375em;
      }
    }
    &__title{
      font-size: 2em;
      padding-top: .075em;
    }
    .spoiler__wrap{
      padding: .625em 0 0 3.5em;
    }
  }
}


